const convertPrice = (price) => {
    const priceArray = price.split("");
    const priceLength = Math.floor(priceArray.length / 3);
    let thousands = [];
    let hundred = [];

    // i qua t cm s gm 3 s ln lt
    for (var i = 0; i <= priceLength; i++) {
        if (i === 0) {
            const a = priceArray.slice(-3 * (i + 1)).join("");
            thousands.push(a);
        }

        if (0 < i && i <= priceLength) {
            const a = priceArray.slice(-3 * (i + 1), -3 * i).join("");
            hundred.push(a);
        }
    }

    // lc b ht cc phn t rng '' trong mng
    const hundreds = hundred.filter((i) => {
        return i !== "";
    });

    // thm 1 phn t rng '' vo u mng  khi ni phn t trong mng c 1 phn t trng  cui to thm du .
    hundreds.unshift("");

    // chui s cui cng
    let priceCovenrt = `${hundreds.reverse().join(".")}${thousands.join()}`;
    return priceCovenrt;
};

export default convertPrice;
